@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Outfit", sans-serif;
}
body {
  background-color: cadetblue;
}
a {
  text-decoration: none;
  color: rgb(128, 74, 74);
}
ul {
  list-style-type: none;
}
.btn {
  padding: 2px 28px;
  font-size: 1rem;

  background-color: rgba(153, 65, 109, 0.884);
  color: #222;
  font-weight: 600;

  cursor: pointer;
}
.btn:hover {
  background-color: rebeccapurple;
  color: #fff;
  transition: 0.3s;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 10;
}
h1 {
  font-family: sans;
  color: rgb(139, 86, 113);
}
.header-bg {
  background-color: blueviolet;
  transition: 0.5s;
}
.nav-menu {
  display: flex;
}
.nav-menu li {
  padding: 0 1rem;
}
.nav-menu li a {
  font-size: 1.5rem;
  font-weight: 500;
}
.humburger {
  display: none;
}
@media screen and (max-width: 1040px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;
    background-color: rgb(111, 191, 194);
  }
  .nav-menu.active {
    left: 0;
  }
  .nav-menu li {
    padding: 1rem 0;
  }
  .nav-menu li a {
    font-size: 2rem;
  }
  .humburger {
    display: initial;
  }
}

.flower2 {
  width: 100%;
  height: 100vh;
  position: relative;
}
.flower-img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}
.flower2::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: cadetblue;
  opacity: 0.7;
}
.flower {
  height: 100%;
  width: 100%;
}
.flower .heading {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}
.flower .heading h1 {
  font-size: 4rem;
  color: rgb(131, 69, 189);
  padding: 0.6rem 1rem;
  font-family: sans;
}
.flower .heading p {
  font-size: 1.4rem;
  font-weight: 200;
  padding: 0.6rem 1rem;
  text-transform: uppercase;
}
.heading .btn {
  border-radius: 8px;
  margin: 1rem 0.2rem;
  padding-top: 4px;
  padding-bottom: 4px;
}
@media screen and (max-width: 640px) {
  .flower .heading h1 {
    font-size: 2rem;
    color: rgb(131, 69, 189);
    padding: 0.6rem 1rem;
    font-family: sans;
  }
  .flower .heading p {
    font-size: 1.4rem;
    font-weight: 200;
    padding: 0.6rem 1rem;
    text-transform: uppercase;
  }
  .btn:hover {
    background-color: rebeccapurple;
    color: #fff;
    transition: 0.3s;
  }
}

.about {
  color: purple;
  font-size: 3rem;
  text-align: center;

  font-family: sans;
}
.container {
  background-color: rgba(118, 118, 179, 0.267);
  background-size: cover;
  width: 100vw;
}
.about-para {
  color: rgb(31, 88, 88);
  font-size: 1.4rem;
  padding-left: 7px;
  text-align: center;
  padding-bottom: 18px;
}
@media screen and (max-width: 640px) {
  .about {
    color: purple;
    font-size: 2.4rem;
    text-align: center;
    padding-top: 4px;
    font-family: sans;
  }

  .about-para {
    color: rgb(31, 88, 88);
    font-size: 1.2rem;
    padding-left: 7px;
    padding-bottom: 18px;
  }
}

.g {
  background-color: gainsboro;
  width: 100vw;
  background-size: cover;
  padding-top: 10px;
  padding-bottom: 10px;
}
.Gallery {
  text-align: center;
  padding-top: 7px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: blueviolet;
}
.gallery {
  margin: 4rem;
  color: #2a2a2a;
}
.gallery h1 {
  font-size: 3rem;
}
.gallerycard {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.g-card {
  width: 32%;
  background-color: rgb(155, 140, 172);

  text-align: start;
  padding: 1rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.2);
}

.g-image {
  height: 200px;
  overflow: hidden;
  background-image: cover;

  border-radius: 7px;
}
.g-image image {
  width: 350px;
  height: 200px;
  transition: 0.3s ease-in-out;
  border: radius 7px;
}
.g-image:hover img {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
@media screen and (max-width: 640px) {
  .gallery {
    margin: 4rem 2rem;
  }
  .gallerycard {
    flex-direction: column;
  }
  .g-card {
    width: 100%;
    margin-bottom: 1.5rem;
  }
}

.bg {
  background-color: rgb(144, 224, 222);
}
.latest {
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
  color: purple;
  font-family: serif;
  font-size: 3rem;
}
.b-image {
  width: 350px;
  height: 200px;
  padding-top: 10px;
  padding-left: 30px;
  padding-bottom: 15px;
}
.b-image image {
  width: 100%;
  height: 100%;
  border: radius 7px;
  transition: 0.3s ease-in-out;
}
.b-image:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  overflow: hidden;
}
@media screen and (max-width: 640px) {
  .latest {
    text-align: center;
    padding-top: 8px;
    color: purple;
    font-family: serif;
    font-size: 2rem;
  }
}

.container {
  background-color: white;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  padding-bottom: 25px;
  padding-top: 25px;
}
h2 {
  font-family: sans;
  color: palevioletred;
  font-size: 1.4rem;
}
p {
  font-family: sans;
  font-size: 1rem;
  color: blueviolet;
}

.container-wrap {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  width: 25%;
  height: auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 10px;
  text-align: center;
  margin: 10px 15px;
  transition: 0.3s ease-in-out;
}
.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  overflow: hidden;
}
.card img {
  height: auto;
  width: 100%;
}

card h1 {
  max-width: 80%;
}

card p {
  width: 80%;
}
span {
  color: rgb(93, 168, 152);
}
@media screen and (max-width: 500px) {
  .container {
    margin: 10px 0px;
    display: flex;
    justify-content: center;
  }

  .container-wrap {
    padding: 20px 0px;
    width: 95%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .card {
    width: 80%;
    height: auto;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 10px;
    text-align: center;
    margin: 10px 15px;
  }
  h2 {
    font-family: sans;
    color: palevioletred;
    font-size: 1.2rem;
  }
  p {
    font-family: sans;
    font-size: 1rem;
    color: blueviolet;
  }
}

.contact-form {
  margin: 4rem 6rem;
  background-color: rgb(62, 119, 121);
}

.contact-form h1 {
  padding-top: 8px;
  text-align: center;
  font-size: 26px;
  color: rgb(33, 45, 61);
}
.contact-form form {
  display: flex;
  padding-top: 3rem;
  flex-direction: column;
  width: 50%;
  margin: auto;
  padding-bottom: 12px;
}
.contact-form input {
  height: 3rem;
  padding: 0 1;
  margin-bottom: 2rem;
  border-radius: 4px;
  border: 1px solid #585757;
}
.contact-form textarea {
  padding: 1rem;
  margin-bottom: 2rem;
  border-radius: 4px;
  border: 1px solid #9b5555;
}
.contact-form button {
  text-align: center;
  display: inline;
  background-color: rgb(124, 35, 207);
  border-radius: 5px;
  color: floralwhite;
  border: 0px solid #2a2a2a;
  width: 150px;
}
@media screen and (max-width: 850px) {
  .contact-form {
    margin: 4rem 2rem;
  }
  .contact-form form {
    padding-top: 3rem;

    width: 90%;
  }
}

.footer {
  width: 100%;
  height: 130px;
  padding-top: 5px;
}
.flower-heading {
  font-family: serif;
  color: rgb(123, 74, 168);
  font-weight: 600;
  font-size: 2.4rem;
}
a {
  font-size: 1.4rem;
}
.text {
  height: 20px;
  font-size: 1rem;
}
button {
  font-size: 1rem;
  background-color: rgba(153, 65, 109, 0.884);
  font-weight: 600;
  border-color: rgba(153, 65, 109, 0.884);
  cursor: pointer;
  border-style: none;
  border-radius: 3px;
  width: 90px;
  margin: 3px;
}
button:hover {
  background-color: rebeccapurple;
  color: #fff;
  transition: 0.3s;
}
@media screen and (max-width: 640px) {
  .text {
    height: 25px;
    width: 50%;
    font-size: 1rem;
  }
  button {
    font-size: 1rem;

    margin: 1rem 0.2rem;
    background-color: rgba(153, 65, 109, 0.884);
    font-weight: 300;
    border-color: rgba(153, 65, 109, 0.884);
    cursor: pointer;
    box-shadow: 2px rgba(153, 65, 109, 0.884);
  }
  button:hover {
    background-color: rebeccapurple;
    color: #fff;
    transition: 0.3s;
  }
}

.flower-image {
  width: 100%;
  height: 60vh;
  position: relative;
}
.flower-image::before {
  content: "";
  background: url("https://media.istockphoto.com/id/1358494633/photo/violet-verbena-field-flower-background.jpg?b=1&s=170667a&w=0&k=20&c=GMHX8daeHWH0Ns0iPhfqDEpKreNpC1brB_hwfFxOP7c=");
  background-size: cover;
  top: 0;
  background-position: center top;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}
.heading {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(96, 65, 124);
}
.flower-image h1 {
  font-size: 2.4rem;
  text-align: center;
}
@media screen and (max-width: 640px) {
  .flower-image h1 {
    font-size: 2rem;
    text-align: center;
  }
}

